import React, { useState } from "react";
import { Avatar } from "antd";
import { NavLink as NavLinkRRD } from "react-router-dom";
import {
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Nav,
  Navbar,
  NavbarBrand,
  NavbarToggler,
  NavItem,
  NavLink,
  Row,
  UncontrolledDropdown,
} from "reactstrap";

// Logo
import Logo from "../../assets/img/logo.png";
const Header = ({ userdata }) => {
  const [isOpen, setIsOpen] = useState(false);
  const user = userdata && userdata;
  return (
    <div className="Header">
      <div className="top_bar">
        <>
          <Row>
            <Col lg="6"></Col>
            <Col lg="6">
              <ul className="top_menu">
                <li>
                  <a href="tel:+918385842313">
                    <i className="fas fa-phone" />
                    +91-8385842313
                  </a>
                </li>
                <li>
                  <a href="mailto:info@pujyapanditg.com">
                    <i className="fas fa-envelope" /> info@pujyapanditg.com
                  </a>
                </li>
              </ul>
            </Col>
          </Row>
        </>
      </div>
      <Navbar color="light" expand="md" light>
        <NavbarBrand to="/" tag={NavLinkRRD}>
          <img
            src={Logo}
            style={{
              width: "200px",
              position: "absolute",
              top: "5px",
              zIndex: "9",
            }}
            alt="Logo"
          />
        </NavbarBrand>
        <NavbarToggler onClick={() => setIsOpen(!isOpen)} />
        <Collapse navbar isOpen={isOpen}>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink to="/" exact tag={NavLinkRRD} activeClassName="active">
                Home
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink to="/about" tag={NavLinkRRD} activeClassName="active">
                About
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to="/all-product"
                tag={NavLinkRRD}
                activeClassName="active"
              >
                Our Product
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to="/all-pooja"
                tag={NavLinkRRD}
                activeClassName="active"
              >
                Pooja
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to="/all-article"
                tag={NavLinkRRD}
                activeClassName="active"
              >
                Article
              </NavLink>
            </NavItem>
            {!user ? (
              <>
                <NavItem>
                  <NavLink
                    to="/login"
                    tag={NavLinkRRD}
                    className="btn btn-amber btn-rounded px-4 text-white"
                  >
                    Login
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    to="/register"
                    tag={NavLinkRRD}
                    className="btn btn-outline-amber btn-rounded px-4"
                  >
                    Register
                  </NavLink>
                </NavItem>
              </>
            ) : (
              <>
                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret>
                    <Avatar
                      src={require("../../assets/img/abt.png")}
                      size={30}
                    />{" "}
                    {user.name}
                  </DropdownToggle>
                  <DropdownMenu right>
                    <DropdownItem
                      to="/profile"
                      className="m-0"
                      tag={NavLinkRRD}
                    >
                      Profile
                    </DropdownItem>
                    <DropdownItem to="/orders" className="m-0" tag={NavLinkRRD}>
                      Orders
                    </DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem
                      href="/"
                      className="m-0"
                      onClick={() => sessionStorage.removeItem("Authtoken")}
                    >
                      Logout
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              </>
            )}
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
};
export default Header;
